.ss-app-uploader-small-frame {
  max-width: 180px;
  display: block;
  > div {
    text-align: center;
    border: 1px solid @gray4;
    border-radius: 5px;
    padding: 20px;
    position: relative;
  }

  .uploader-close-click {
    position: absolute;
    top: 4px;
    right: 4px;
  }

  .uploader-text-click, .uploader-close-click {
    cursor: pointer;
    display: inline-block;
  }
}